<template>
  <div class="sub_module">
    <div class="module_header">
      <div class="title">学生提问</div>
      <div class="more" v-if="studentQuestion.length>0" @click="dialogVisible=true">
        查看详情
        <i class="iconfont icon-jiantou"></i>
      </div>
    </div>

    <el-row :gutter="20">
      <el-col :span="12" v-for="(item, index) in scorePreview" :key="index">
        <el-card shadow="hover" class="list_card blue_bg">
          <el-row :gutter="20">
            <el-col :span="6">
              <img src="/static/boy_img.png" alt=""/>
              <div>{{item.StudentName}}</div>
            </el-col>
            <el-col :span="18" class="card_info">
              <div class="card_line" v-for="(sub, k) in item['Questions']" :key="k">
                <div>{{formatDate(sub['CreateTime'], 'hh:mm:ss')}}</div>
                <p> {{sub['QContent']}}</p>
              </div>
            </el-col>
          </el-row>
        </el-card>
      </el-col>
    </el-row>

    <el-dialog title="学生提问记录" :visible.sync="dialogVisible">
      <el-row>
        <el-col :span="18">
          <!-- 提问记录 -->
          <div class="chat" ref="chat">

            <div v-if="dataList && dataList.length>0" class="chat_content">
              <div v-for="(sub, k) in dataList" :key="k"
                   :class=" sub['StudentID']!== selectStudentId ? 'chat_item' : 'chat_item checked_item'">
                <div class="chat_news">

                  <div class="chat_news_name">
                    {{sub['StudentName']}}
                    <span>{{formatDate(sub['CreateTime'], 'hh:mm:ss')}}</span>
                  </div>
                  <div class="chat_news_cont">
                    <span>{{sub['QContent'] }}</span>
                  </div>
                </div>
              </div>
            </div>

          </div>
        </el-col>
        <el-col :span="6">
          <div class="summary">提问人员</div>
          <div class="list_person">
            <div v-for="(item, index) in studentQuestion" :key="index" class="person_item"
                 :class="selectStudentId === item['StudentID'] ? 'person_selected' : ''"
                 @click="selectStudentId=item['StudentID']">
              <img :src="defaultPhotoImg" alt=""/>
              <span>{{item['StudentName']}}</span>
              <span></span>
            </div>

          </div>
        </el-col>
      </el-row>
    </el-dialog>
  </div>
</template>

<script>
  import commonTable from "@/components/commonTable";
  import { formatDate } from "@/utils/utils";
  import imgPhoto from "./../../../../../../../static/boy_img.png";

  export default {
    name: "StudentQuestion",
    components: { commonTable },
    data() {
      return {
        course: JSON.parse(sessionStorage.getItem("course")),
        courseID: sessionStorage.getItem("courseID"),
        dataList: [],
        studentQuestion: [],

        defaultPhotoImg: imgPhoto,
        selectStudentId: "",
        listLoading: false,
        dialogVisible: false,
      };
    },
    created() {
      this.GetStudentQuestion();
    },
    methods: {
      formatDate(dateValue, format) {
        return formatDate(dateValue, format);
      },
      //获取学生提问
      GetStudentQuestion() {
        this.dataList = [];
        this.studentQuestion = [];
        this.$http
          .get(this.$api.GetQuestionAllDatas, {
            courseID: this.courseID,
            classID: this.course.ClassID,
            attribute: 2 // 课中
          })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.dataList.push(item);
                let index = this.studentQuestion.findIndex(r => r.StudentID === item["StudentID"]);
                if (index > -1) {
                  this.studentQuestion[index].Questions.push(item);
                } else {
                  this.studentQuestion.push({
                    StudentID: item["StudentID"],
                    StudentName: item["StudentName"],
                    Questions: [item]
                  });
                }
              });
            }
          });
      }
    },
    computed: {
      scorePreview() {
        return this.studentQuestion.filter((r, k) => k < 6);
      }
    }
  }
</script>

<style lang="less" scoped>
  .list_card {
    text-align: center;
    margin-bottom: 10px;

    .card_info {
      color: #2c3e50;
      background-color: #fff;
      border-radius: 10px;
      height: 106px;
      overflow-y: auto;
      word-break: break-all;
      word-wrap: break-word;

      .card_line {
        text-align: left;
        font-size: 12px;
        line-height: 1.5;

        div {
          color: #2a9efc;
        }

        p {
          font-size: 14px;
          padding-left: 10px;
          /*text-indent: 10px;*/
        }
      }
    }

  }

  .chat {
    padding: 0 5px;
    text-align: left;
    width: 100%;
    max-height: 600px;
    overflow-y: auto;
    background: rgba(10, 10, 10, 0.1);
    border: 1px solid #f9f9f9;

    .chat_content {
      .chat_item {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;

        .chat_news {
          .chat_news_name {
            font-size: 12px;
            line-height: 2.5;
            color: #409EFF;
          }

          .chat_news_cont {
            padding: 6px 5px 6px 18px;
            font-size: 14px;
            line-height: 1.5;

            text-align: left;
            word-break: break-all;
            /*border: 1px solid #777;*/
            /*border-radius: 7px;*/

            img {
              max-width: 45vw;
              max-height: 25rem;
            }
          }
        }
      }

      .checked_item {
        .chat_news {
          .chat_news_name {
            font-weight: bolder;
            color: #67C23A;
          }

          .chat_news_cont {
            color: #777;
          }
        }
      }
    }
  }

  .summary {
    text-align: center;
    font-size: 14px;
    color: rgba(153, 153, 153, 1);
    line-height: 1.5;
  }

  .list_person {
    max-height: 600px;
    overflow-y: auto;

    .person_item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 5px;
      line-height: 40px;

      &:hover {
        background-color: #f5f7fa;
      }

      img {
        max-width: 25px;
        max-height: 25px;
        border-radius: 50%;
      }
    }
  }

  .person_selected {
    background-color: #f1f1fa;
  }
</style>



