<template>

  <div class="practice_detail">
    <div class="head">
      <!--        <div class="part_title">随堂练习</div>-->
      <!--        <el-row class="part_line">-->
      <!--          <el-col :span="6">名称：{{cpdata.CPTitle}}</el-col>-->
      <!--          <el-col :span="6">题数：{{cpdata.SubjectTotal}}</el-col>-->
      <!--          <el-col :span="6">总分：{{cpdata.SubjectScore}}</el-col>-->
      <!--          <el-col :span="6">计时(分)：{{cpdata.CPTime}}</el-col>-->
      <!--        </el-row>-->

      <el-carousel trigger="click" arrow="always" :autoplay="false" :height="autoHeight" @change="selectData"
                   style="padding:10px 0;">
        <el-carousel-item v-for="item in parseInt(cpdata.SubjectTotal||'0')" :key="item">
          <div class="mb" :ref="'carousel'+item">
            <div class="mb">{{psdata.PSSerial}}. [{{fetchSSType(psdata.PSType)}}] {{psdata.PSScore}}分
            </div>
            <div class="mb">{{psdata.PSContent}}</div>
            <div class="mb" v-if="psdata.PSType !== 3">
              <div v-for="(item,index) in sodatas" :key="index" :item="item">{{item.SOSerial}}. {{item.SOContent}}
              </div>
            </div>
            <div class="mb">
              答案：
              <span class="primary">{{psdata.PSAnswer}}</span>
            </div>
            <div class="mb">
              解析：
              <span class="danger">{{psdata.PSanalysis}}</span>
            </div>
          </div>
        </el-carousel-item>
      </el-carousel>

      <div class="part_title">学生答题统计</div>
      <div class="part_line">
        <el-row v-if="subjectType !== 4">
          <el-col :span="10">
            <r-chart ref="rchart" title="学生答题情况" :data="answerStatistics" @chartChange="selectChart"></r-chart>
          </el-col>
          <el-col :span="14">
            <person-card title="答题人员列表" :data="answerPersons" :pictureType="7"></person-card>
          </el-col>
        </el-row>
        <el-row v-else>
          <el-col :span="24">
            <person-card ref="Appraise" showDetail title="对应答题人员列表" :data="answerList" :subject-type="subjectType"
                         @submitSave="saveAppraise" :pictureType="7"></person-card>
          </el-col>
        </el-row>
      </div>

      <div class="part_title">问卷调研</div>
      <div class="part_line">
        <el-row>
          <el-col :span="2" class="line_left">已掌握</el-col>
          <el-col :span="18">
            <el-progress :percentage="surveyInfo.yesPer||0" :text-inside="true" :stroke-width="24"
                         status="success"></el-progress>
          </el-col>
          <el-col :span="4" class="line_right">{{surveyInfo.yes}}人</el-col>
        </el-row>
        <el-row>
          <el-col :span="2" class="line_left">未掌握</el-col>
          <el-col :span="18">
            <el-progress :percentage="surveyInfo.noPer||0" :text-inside="true" :stroke-width="24"
                         status="exception"></el-progress>
          </el-col>
          <el-col :span="4" class="line_right">{{surveyInfo.no}}人</el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>
  import RChart from "./RChart";
  import PersonCard from "./PersonCard";
  import imgPhoto from "@/assets/boy_img.png";

  export default {
    components: { RChart, PersonCard },
    props: {
      cpdata: {
        type: Object,
        default: () => {
          return {};
        }
      }
    },
    data() {
      return {
        // cpdata: JSON.parse(sessionStorage.getItem("cpdata")),
        psdata: Object,
        sodatas: [],
        psserial: 1,
        autoHeight: "300px",
        selectIndex: "0",
        subjectId: "",
        subjectAnswer: "",
        subjectType: "",
        answerStatistics: [],
        answerPersons: [],
        answerList: [],
        surveyInfo: {}
      };
    },
    created() {
    },
    methods: {
      //获取题目
      getSubjectData() {
        this.psdatas = [];
        this.sodatas = [];
        this.$http
          .get(this.$api.GetSubjectData, {
            plid: this.cpdata.PLID,
            psserial: this.psserial
          })
          .then(res => {
            if (res.Result && res.Data) {
              this.psdata = res.Data;
              this.subjectId = this.psdata["PSID"];
              this.subjectType = this.psdata["PSType"];
              if (res.SODatas && res.SODatas.length > 0) {
                res.SODatas.forEach(item => {
                  this.sodatas.push(item);
                });
              }
            }
            this.answerStatistics = [];
            this.answerPersons = [];
            this.answerList = [];
            if (this.subjectType !== 4) {
              this.getAnswerStatistics();
            } else {
              this.getAnswerList();
            }
            this.getSurveyList();
            this.initHeight();
          })
          .catch(err => {
            this.initHeight();
          });
      },
      getAnswerStatistics() {
        this.$http
          .get(this.$api.ClassPractiseAnswerStatistics, {
            cpid: this.cpdata["CPID"],
            psid: this.subjectId
          })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              let total = 0;
              res.Datas.forEach(r => {
                total += r["Total"] || 0;
              });

              let active = -1;
              res.Datas.forEach((item, k) => {
                let per = 0;
                if (total !== 0) per = Math.round((10000.0 * (item["Total"] || 0)) / total) / 100;
                let sub = {
                  name: item["OptionName"],
                  value: item["Total"],
                  percentage: per
                };
                this.answerStatistics.push(sub);
                if (active === -1 && (item["Total"] || 0) > 0) active = k;
              });
              if (active > -1 && this.subjectType !== 4)
                this.$refs["rchart"].selectChart(
                  this.answerStatistics[active],
                  active
                );
            }
          })
          .catch(err => {
          });
      },
      getAnswerDetail() {
        this.answerPersons = [];
        this.$http
          .get(this.$api.ClassPractiseAnswerDetail, {
            cpid: this.cpdata["CPID"],
            psid: this.subjectId,
            answer: this.subjectAnswer
          })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                let sub = {
                  id: item.Key["StudentID"],
                  name: item.Key["StudentName"],
                  photo: imgPhoto,
                  gender: item.Key["Gender"],
                  no: item.Key["Sno"]
                };
                this.answerPersons.push(sub);
              });
            }
          })
          .catch(err => {
          });
      },
      getAnswerList() {
        this.answerList = [];
        this.$http
          .get(this.$api.ClassPractiseAnswerList, {
            courseID: sessionStorage.getItem("courseID"),
            cpid: this.cpdata["CPID"],
            psid: this.subjectId
          })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                let sub = {
                  id: item.Key["StudentID"],
                  name: item["StudentName"],
                  photo: imgPhoto,
                  time: item.Key["SubmitTime"],
                  score: item.Key["PAScore"],
                  type: item.Key["PAForm"],
                  answerId: item.Key["PAID"],
                  content: item.Key["PAContent"],
                  comment: item.Key["PAComment"]
                };
                this.answerList.push(sub);
              });
            }
          })
          .catch(err => {
          });
      },
      getSurveyList() {
        this.surveyInfo = {
          yes: 0,
          yesPer: 0,
          no: 0,
          noPer: 0,
          total: 0
        };
        this.$http
          .get(this.$api.ClassPractiseSurveyInfo, {
            courseID: sessionStorage.getItem("courseID"),
            cpid: this.cpdata["CPID"],
            psid: this.subjectId
          })
          .then(res => {
            if (res.Result) {
              this.surveyInfo = {
                yes: res["YesMaster"] || 0,
                yesPer: 0,
                no: res["NoMaster"] || 0,
                noPer: 0,
                total: 0
              };
              this.surveyInfo.total = this.surveyInfo.yes + this.surveyInfo.no;
              this.surveyInfo.yesPer = Math.round(
                (this.surveyInfo.yes * 100.0) / this.surveyInfo.total
              );
              this.surveyInfo.noPer = Math.round(
                (this.surveyInfo.no * 100.0) / this.surveyInfo.total
              );
            }
          })
          .catch(err => {
          });
      },

      //获取题目类型
      fetchSSType(val) {
        let typeName = "";
        switch (val) {
          case 1:
            typeName = "单选题";
            break;
          case 2:
            typeName = "多选题";
            break;
          case 3:
            typeName = "判断题";
            break;
          case 4:
            typeName = "问答题";
            break;
        }

        return typeName;
      },
      selectData(val) {
        // console.log("total:", this.cpdata.SubjectTotal);
        this.psserial = val + 1;
        this.getSubjectData();
      },
      initHeight() {
        setTimeout(() => {
          if (!this.$refs["carousel" + this.psserial]) {
            this.autoHeight = 300 + "px";
          } else {
            this.autoHeight =
              this.$refs["carousel" + this.psserial][0].clientHeight + "px";
          }
        }, 200);
      },
      selectChart(index) {
        this.subjectAnswer = this.answerStatistics[index].name;
        this.getAnswerDetail();
      },
      saveAppraise(form) {
        this.$refs["Appraise"].btnLoading = true;
        this.$http
          .post(this.$api.ClassPractiseScoreSave, {
            paid: form.answerId,
            paScore: form.score,
            comment: form.comment
          })
          .then(res => {
            if (res.Result) {
              this.$message({
                message: "操作成功！",
                type: "success"
              });
              let answer = this.answerList.find(
                r => r.answerId === form.answerId
              );
              answer.score = form.score;
              answer.comment = form.comment;
              this.$refs["Appraise"].dialogShow = false;
            } else if (res.message) {
              this.$message({
                showClose: true,
                ...res
              });
            }
            this.$refs["Appraise"].btnLoading = false;
          })
          .catch(err => {
            this.$refs["Appraise"].btnLoading = false;
          });
      }
    }
  };
</script>

<style lang='less' scoped>
  .practice_detail {
    width: 100%;
    text-align: left;

    .head {
      padding: 0 20px;
      background: rgba(255, 255, 255, 1);
      font-size: 14px;

      .part_title {
        border-left: 2px solid #2a9efc;
        padding-left: 5px;
        margin: 5px 0;
      }

      .part_line {
        border-bottom: 2px solid #f1f5f9;

        .el-row {
          padding-bottom: 10px;
        }

        .line_left {
          text-align: right;
          padding-right: 10px;
        }

        .line_right {
          padding-left: 10px;
        }
      }

      .mb {
        margin-bottom: 15px;

        .primary {
          color: #409eff;
        }

        .danger {
          color: #f56c6c;
        }
      }
    }
  }
</style>



