<template>
  <div class="record_module">
    <div class="module_header">
      <div class="name">考勤统计</div>
      <div class="more" @click="MoreSignRanking()">
        查看更多
        <i class="iconfont icon-jiantou"></i>
      </div>
    </div>
    <el-row class="nr">
      <el-col :span="12" class="left">
        <div class="personal-chart" id="classManaChart"></div>
      </el-col>
      <el-col :span="12" class="right">

        <common-table :columns="tableColumns" :datas="signPreview" th-class="table-innerhead" border
                      v-loading="listLoading" :select="false" :pagger="false">
          <template slot="signTime" slot-scope="scope">
            <span>{{formatDate( scope.row.signTime, 'hh:mm:ss')}}</span>
          </template>
          <template slot="signState" slot-scope="scope">
            <span>{{fetchSignState(scope.row.signState)}}</span>
          </template>
        </common-table>
      </el-col>
    </el-row>

    <el-dialog title="考勤明细" :visible.sync="dialogTableVisible" style="height:800px" :before-close="resetForm">
      <div style="display: flex;justify-content: space-between;margin-bottom: 10px;margin-top: -20px;">
        <div>修改考勤：先勾选学生，再选择考勤状态，点击批量修改。</div>
        <div>
          <el-select v-model="signstate" size="small" placeholder="请选择考勤状态">
            <el-option v-for="item in signStatus" :key="item.value" :label="item.text" :value="item">
            </el-option>
          </el-select>
          <el-button type="primary" size="small" @click="UpdateState">批量修改</el-button>
        </div>
      </div>
      <common-table
        :columns="tableColumns"
        :datas="signDatas"
        th-class="table-innerhead"
        border
        v-loading="listLoading"
        :select="true"
        :pagger="false"
        max-height="550"
        @selectHandler="selectChange"
        :data-id="sid">
        <template slot="signTime" slot-scope="scope">
          <span>{{formatDate( scope.row.signTime, 'hh:mm:ss')}}</span>
        </template>
        <template slot="signState" slot-scope="scope">
          <el-tag
            :type="scope.row.signState === 1 ? 'primary' : scope.row.signState === 2 ? 'success' : scope.row.signState === 3 ? 'warning' : 'danger'"
            disable-transitions>{{fetchSignState(scope.row.signState)}}
          </el-tag>
        </template>
      </common-table>
    </el-dialog>

  </div>
</template>

<script>
  import commonTable from "@/components/commonTable";
  import { formatDate } from "@/utils/utils";
  import "echarts/lib/chart/pie";
  import "echarts/lib/component/tooltip";
  import "echarts/lib/component/graphic";
  import "echarts/lib/component/legend";

  let echarts = require("echarts/lib/echarts");

  export default {
    name: "Attendance",
    components: { commonTable },
    data() {
      return {
        selectedIds: [],
        sid: "sid",
        course: JSON.parse(sessionStorage.getItem("course")),
        courseID: sessionStorage.getItem("courseID"),
        SignStats: {
          Late: 0,
          Leave: 0,
          Normal: 0,
          Total: 0,
          Truancy: 0
        },
        signDatas: [],
        selectedList: [],
        signstate: "",
        signStatus: [
          { text: "正常", value: 1 },
          { text: "迟到", value: 2 },
          { text: "旷课", value: 0 },
          { text: "请假", value: 3 }
        ],
        tableColumns: [
          {
            prop: "studentName",
            label: "学生姓名",
            sort: true
          },
          {
            prop: "signTime",
            slot: true,
            label: "签到时间",
            sort: true
          },
          {
            prop: "signState",
            slot: true,
            label: "状态",
            sort: true
          }
        ],
        listLoading: false,
        dialogTableVisible: false
      };
    },
    created() {
      this.GetSignStats();
      this.GetSignDatas();
    },
    methods: {
      formatDate(dateValue, format) {
        return formatDate(dateValue, format);
      },
      selectChange(selected) {
        this.selectedIds = selected;
        this.selectedList = [];
        this.selectedIds.forEach(item => {
          this.signDatas.forEach(element => {
            if (element.sid == item) {
              this.selectedList.push(element);
            }
          });
        });
      },
      // 签到统计
      GetSignStats() {
        this.$http
          .get(this.$api.GetSignStats, {
            courseID: this.courseID
          })
          .then(res => {
            if (res.Result) {
              this.SignStats = {
                Late: res["Late"] || 0,
                Leave: res["Leave"] || 0,
                Normal: res["Normal"] || 0,
                Total: res["Total"] || 0,
                Truancy: res["Truancy"] || 0
              };
            }
            this.$nextTick(() => {
              this.drawClassManaChart();
            });
          })
          .catch(err => {
            this.$nextTick(() => {
              this.drawClassManaChart();
            });
          });
      },
      //获取签到列表
      GetSignDatas() {
        this.signDatas = [];
        this.listLoading = true;
        this.$http
          .get(this.$api.GetSignDatas, {
            courseID: this.courseID,
            classID: this.course.ClassID
          })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach((item, index) => {
                this.signDatas.push({
                  sid: index,
                  SignID: item.Value == null ? "" : item.Value.SignID,
                  studentID: item.Key.StudentID,
                  studentName: item.Key.StudentName,
                  signTime: item.Value == null ? "" : item.Value.SignTime,
                  signState: item.Value == null ? -1 : item.Value.SignState
                });
              });
            }
            this.listLoading = false;
          })
          .catch(err => {
            this.listLoading = false;
          });
      },
      resetForm() {
        this.signstate = "";
        this.dialogTableVisible = false;
      },
      UpdateState() {
        if (this.signstate == "") {
          this.$message({
            dangerouslyUseHTMLString: true,
            showClose: true,
            message: "请选择一个状态",
            type: "warning"
          });
          return;
        }
        if (this.selectedList.length == 0) {
          this.$message({
            dangerouslyUseHTMLString: true,
            showClose: true,
            message: "请选择一条要修改的记录",
            type: "warning"
          });
          return;
        }
        this.$http
          .post(this.$api.UpdateSignDatas, {
            signstate: this.signstate.value,
            courseID: this.courseID,
            selectedList: JSON.stringify(this.selectedList)
          })
          .then(res => {
            if (res.Result) {
              this.dialogTableVisible = false;
              this.GetSignDatas();
            }
          })
          .catch(err => {
          });
      },
      //获取签到状态
      fetchSignState(val) {
        let item = this.signStatus.find(r => r.value === val);
        return item ? item.text : "旷课";
      },
      //更多签到
      MoreSignRanking() {
        this.dialogTableVisible = true;
      },
      filterState(value, row) {
        this.signStatusName = this.fetchSignState(value);
        return row.signState === value || (value === 0 && row.signState === -1);
      },
      drawClassManaChart() {
        let data = [
          { name: "正常", value: this.SignStats.Normal },
          { name: "迟到", value: this.SignStats.Late },
          {
            name: "旷课",
            value: this.SignStats.Truancy < 0 ? 0 : this.SignStats.Truancy
          },
          { name: "请假", value: this.SignStats.Leave }
        ];
        let legend = [];
        data.forEach((element, i) => {
          legend.push(element.name);
        });
        let options = {
          color: ["#2A9EFC", "#71C847", "#FA7777", "#EA9855"],
          title: {
            text: "",
            textStyle: {
              rich: {
                a: {
                  padding: 20,
                  fontSize: 20,
                  fontWeight: "bold"
                },
                b: {
                  color: "#37A3FC",
                  fontSize: 16
                }
              }
            },
            x: "40%",
            y: "8%"
          },
          grid: {
            top: "1%",
            bottom: "1%",
            left: "1%",
            right: "1%"
          },
          legend: {
            top: "38%",
            left: "49%",
            itemGap: 40,
            textStyle: {
              fontSize: 14
            },
            data: legend
          },
          tooltip: {
            show: true,
            trigger: "item",
            formatter: "{b}：{c} <br/>占比：{d}%"
          },
          series: [
            {
              zlevel: 1,
              name: "类型",
              type: "pie",
              radius: ["69%", "90%"],
              center: ["23%", "50%"],
              hoverAnimation: false,
              animation: false,
              label: {
                normal: {
                  show: false
                }
              },
              // itemStyle: {
              //   normal: {
              //     shadowColor: 'rgba(0, 0, 0, 0.8)',
              //     shadowBlur: 50,
              //   }
              // },
              data: data
            },
            {
              radius: ["69%", "90%"],
              center: ["23%", "50%"],
              type: "pie",
              name: "类型",
              hoverAnimation: false,
              animation: false,
              data: [
                {
                  value: [this.SignStats.Total],
                  name: "总人数",
                  label: {
                    normal: {
                      show: true,
                      formatter: "{c|{c}}\n {a|总人数}",
                      rich: {
                        c: {
                          color: "#000",
                          fontSize: 50,
                          fontWeight: "bold",
                          height: 60
                        },
                        a: {
                          align: "center",
                          color: "#999999",
                          fontSize: 18,
                          height: 20
                        }
                      },
                      position: "center"
                    }
                  },
                  labelLine: {
                    show: false
                  }
                }
              ]
            }
          ]
        };
        let chart = echarts.init(document.getElementById("classManaChart"));
        chart.setOption(options);
      }
    },
    computed: {
      signPreview() {
        return this.signDatas.filter((r, k) => k < 4);
      }
    }
  };
</script>

<style lang="less" rel="stylesheet/less" scoped>
  .personal-chart {
    width: 90%;
    height: 200px;
  }

  .personnal-detail-wrap {
    &-head {
      width: 100%;
      height: 52px;
      border-bottom: 1px solid #eaedf2;
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      margin-bottom: 15px;

      &-add {
        float: right;
        font-size: 0.9rem;
        color: #2a9efc;
        padding: 8px 10px;
        border: 1px solid #2a9efc;
        border-radius: 5px;
        user-select: none;
        cursor: pointer;

        .iconfont {
          vertical-align: middle;
        }
      }
    }
  }
</style>



