<template>
  <div id="class-record">
    <div class="main">
      <!--课程信息-->
      <course-info :course="course"/>

      <div class="content">
        <attendance></attendance>
        <course-comment></course-comment>
        <group-score :group-list="groupingDatas"></group-score>
        <student-score></student-score>

        <div class="record_module">
          <div class="module_header">
            <div>课堂互动</div>
          </div>
          <student-question></student-question>
          <screen-question></screen-question>
          <class-practice></class-practice>
          <random-person></random-person>
          <group-discussion :group-list="groupingDatas"></group-discussion>
        </div>

        <div class="ktzy">
          <div class="tt">课堂资源</div>
          <div class="bt zlbk">课件</div>
          <common-table :columns="coursewareColumns" :datas="courseWareDatas" th-class="table-innerhead" border
                        max-height="550" :select="false" :pagger="false" style="margin-bottom:20px;">
            <template slot="MaterialName" slot-scope="scope" align="center">
              {{scope.row.Value.MaterialName + '.'+scope.row.Value.MaterialFileExt}}
            </template>
            <template slot="Attribute" slot-scope="scope" align="center">
              {{getCourseWareAttribute(scope.row.Key.Attribute)}}
            </template>
            <template slot="CreateTime" slot-scope="scope" align="center">
              {{formatDate(scope.row.Key.CreateTime , 'yyyy-MM-dd hh:mm:ss')}}
            </template>
            <template slot="oprate" slot-scope="scope" align="center">
              <el-button type="primary" size="mini" @click="courseWareComment(scope.row)">学生点评记录</el-button>
            </template>
          </common-table>
          <!--          <div class="nr">-->
          <!--            <div class="item" v-for="(item,index) in courseWareDatas" :key="index">-->
          <!--              <div class="ct">-->
          <!--                <img src="/static/courseware.png" alt height="60px"/>-->
          <!--                <div class="info">-->
          <!--                  <div class="name">{{item.Value.MaterialName + '.'+item.Value.MaterialFileExt}}</div>-->
          <!--                  <div class="time">{{formatDate(item.Key.CreateTime , 'yyyy-MM-dd hh:mm:ss')}}</div>-->
          <!--                  <el-button type="primary" size="mini" @click="courseWareComment(item)">学生点评记录</el-button>-->
          <!--                </div>-->
          <!--              </div>-->
          <!--            </div>-->
          <!--          </div>-->

          <div class="bt zlbk">板书</div>
          <div class="nr">
            <div class="item" v-for="(item,index) in boardNoteDatas" :key="index" :item="item">
              <div class="ct">
                <div class="cdiv">
                  <el-popover trigger="click">
                    <img :src="item.img" alt="" :style="{maxWidth:'800px', maxHeight:'500px'}"/>
                    <img :src="item.img" slot="reference" alt class="cimg"/>
                  </el-popover>
                </div>
                <div class="info">
                  <div class="time">{{formatDate(item.CreateTime, 'yyyy-MM-dd hh:mm:ss')}}</div>
                </div>
              </div>

            </div>
          </div>

          <div class="bt zlbk">小组笔记</div>
          <div class="nr">
            <div class="item" v-for="(item,index) in groupNoteDatas" :key="index" :item="item">
              <div class="ct">
                <div class="cdiv">
                  <el-popover trigger="click">
                    <img :src="item.img" alt="" :style="{maxWidth:'800px', maxHeight:'500px'}"/>
                    <img :src="item.img" slot="reference" alt class="cimg"/>
                  </el-popover>
                </div>
                <div class="info">
                  <div class="time">{{formatDate(item.CreateTime, 'yyyy-MM-dd hh:mm:ss')}}</div>
                </div>
              </div>
            </div>
          </div>

          <div class="bt zlbk">课堂实况</div>
          <div v-for="(item, index) in microLessonDatas" :key="index">
            <i class="el-icon-video-camera-solid title_first"></i>
            {{item.type==='teacher'?'老师实况':'小组实况'}}
            <div v-for="(sub, k) in item.List" :key="k">
              <i class="el-icon-s-flag title_second"></i>
              {{sub.mark ? sub.mark : '无标记'}}
              <el-row :gutter="20">
                <el-col :span="8" v-for="(sec, i) in sub.List" :key="i"
                        :style="{textAlign: 'center', lineHeight:2}">
                  <!--                  <p>{{sec['MLTitle']||'微课视频文件'}}</p>-->
                  <div class="video-player">
                    <video :ref="'video'+i" preload="false" controls="controls" :poster="sec.poster" :src="sec.src"
                           @canplaythrough="sec.isLoading = false" @error="handleError($event, i)"
                           @stalled="sec.isLoading = true" @waiting="sec.isLoading = true"
                           @playing="vedioChange($event, i)">
                      您的浏览器不支持HTML5
                    </video>
                    <div class="loader" v-show="sec.isLoading">
                      <div class="loader-inner ball-clip-rotate">
                        <div></div>
                      </div>
                    </div>
                    <!-- 错误弹窗 -->
                    <div class="error" v-show="sec.isError">
                      <p class="lose">视频加载失败</p>
                      <p class="retry" @click="retry($event, index)">点击重试</p>
                    </div>
                  </div>
                  <div>{{sec['MLTitle']||'微课视频文件'}}</div>
                </el-col>
              </el-row>
            </div>

          </div>

        </div>
      </div>

      <el-dialog title="课件评价记录" :visible.sync="dialogTableVisible" class="dialog_list">
        <common-table :columns="tableColumns" :datas="CourseWareCommentList" th-class="table-innerhead" border
                      max-height="550"
                      v-loading="listLoading" :select="false" :pagger="false">
          <template slot="StudentName" slot-scope="scope">
            {{scope.row['Datas']['IsAnonymous'] === 1 ? '匿名' : scope.row['StudentName'] }}
          </template>
          <template slot="Score" slot-scope="scope">
            {{scope.row['Datas']['Score']}}
          </template>
          <template slot="Comment" slot-scope="scope">
            {{scope.row['Datas']['Comment']}}
          </template>
          <template slot="SubmitTime" slot-scope="scope">
            {{formatDate(scope.row['Datas']['SubmitTime'], 'yyyy-MM-dd hh:mm')}}
          </template>
        </common-table>
      </el-dialog>

    </div>
  </div>
</template>

<script>
  import commonTable from "@/components/commonTable";
  import defaultBg from "./../../../../../../static/video.png";
  import courseInfo from "../../../../../components/personal/teacher/myCourse/courseInfo.vue";
  import { formatDate } from "@/utils/utils";
  import { ModulePicture } from "@/utils/enum";
  import Attendance from "./component/Attendance";
  import CourseComment from "./component/CourseComment";
  import GroupScore from "./component/GroupScore";
  import StudentScore from "./component/StudentScore";
  import StudentQuestion from "./component/StudentQuestion";
  import ScreenQuestion from "./component/ScreenQuestion";
  import ClassPractice from "./component/ClassPractice";
  import RandomPerson from "./component/RandomPerson";
  import GroupDiscussion from "./component/GroupDiscussion";

  export default {
    name: "",
    components: {
      commonTable,
      courseInfo,
      Attendance,
      CourseComment,
      GroupScore,
      StudentScore,
      StudentQuestion,
      ScreenQuestion,
      ClassPractice,
      RandomPerson,
      GroupDiscussion,
    },
    data() {
      return {
        course: JSON.parse(sessionStorage.getItem("course")),
        courseID: sessionStorage.getItem("courseID"),
        interactConfig: "",
        groupingDatas: [],
        courseWareDatas: [],
        boardNoteDatas: [],
        groupNoteDatas: [],
        microLessonDatas: [],

        checked: false,
        kqDetail: [
          {
            name: "学生"
          }
        ],

        vedioIndex: -1,

        coursewareColumns: [
          { prop: "MaterialName", label: "课件", slot: true },
          { prop: "Attribute", label: "属性", slot: true, width: 80 },
          { prop: "CreateTime", label: "创建时间", slot: true, width: 150 },
          { prop: "oprate", label: "操作", slot: true, width: 160 }
        ],
        CourseWareCommentList: [],
        tableColumns: [
          { prop: "StudentName", slot: true, label: "学生姓名", width: 120 },
          { prop: "Score", slot: true, label: "课件评分", width: 120 },
          { prop: "Comment", slot: true, label: "课件点评" },
          { prop: "SubmitTime", slot: true, label: "创建时间", width: 150 },
        ],
        listLoading: false,
        dialogTableVisible: false,
      };
    },
    computed: {},
    watch: {},
    created() {
      this.GetInteractConfig();
      this.GetGroupingDatas();
      this.GetCourseWareDatas();
      this.GetBoardNoteDatas();
      this.GetGroupNoteDatas();
    },
    mounted() {
    },
    methods: {
      formatDate(dateValue, format) {
        return formatDate(dateValue, format);
      },

      //获取智慧教室配置
      GetInteractConfig() {
        this.$http
          .get(this.$api.GetInteractConfig, { clsRoomID: this.course.ClassroomID })
          .then(res => {
            if (res.Result) {
              this.interactConfig = JSON.parse(res.Data.ConfigInfo);

              this.GetMicroLessonDatas();
            }
          });
      },

      //获取图片
      fetchPic(filename, fileType) {
        return this.$api.InteractDownload + "?fileName=" + filename + "&fileType=" + fileType + "&platFormType=1";
      },

      //获取分组次数
      GetGroupingDatas() {
        this.$http
          .get(this.$api.GetGroupingDatas, { courseID: this.courseID })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.groupingDatas.push(item);
              });
            }
            // console.log("groupingDatas:", this.groupingDatas);
          });
      },

      //获取课件
      GetCourseWareDatas() {
        this.$http
          .get(this.$api.CourseWareList, { courseID: this.courseID, attribute: -1 })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.courseWareDatas.push(item);
              });
            }
          });
      },
      // 课件属性
      getCourseWareAttribute(type) {
        if (type === 1) {
          return "课前";
        } else if (type === 2) {
          return "课中";
        } else if (type === 3) {
          return "课后";
        } else {
          return "未知";
        }
      },

      // 获取课件评价
      courseWareComment(item) {
        this.listLoading = true;
        this.dialogTableVisible = true;
        this.CourseWareCommentList = [];
        this.$http
          .get(this.$api.CoursewareCommentList, { courseID: this.courseID, cID: item.Key["CID"] })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.CourseWareCommentList.push(item);
              });
            }
            this.listLoading = false;
          }).catch(err => {
          this.listLoading = false;
        });
      },

      //获取板书
      GetBoardNoteDatas() {
        this.$http
          .get(this.$api.GetBoardNoteDatas, { courseID: this.courseID })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.boardNoteDatas.push({ ...item, img: this.fetchPic(item["BNContent"], ModulePicture.BoardNote) });
              });
            }
          });
      },

      //获取小组笔记
      GetGroupNoteDatas() {
        this.$http
          .get(this.$api.groupNoteAllDatas, { courseID: this.courseID })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.groupNoteDatas.push({ ...item, img: this.fetchPic(item["GNContent"], ModulePicture.groupNote) });
              });
            }
          });
      },

      //获取微课
      async GetMicroLessonDatas() {
        this.microLessonDatas = [];
        let teacherTypes = [1, 2, 3, 4];
        let teachers = [];
        let students = [];
        let vedioUrl = "http://" + this.interactConfig.RecIP + ":50120";
        this.$http
          .get(this.$api.GetMicroLessonDatas, { courseID: this.courseID })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                let mark = item["RecMark"] + "";
                let sub = {
                  ...item,
                  src: vedioUrl + "/" + item["MLID"] + "." + item["FileFormat"],
                  isLoading: false,
                  isError: false,
                  poster: defaultBg
                };
                if (teacherTypes.indexOf(item['MLType']) > -1) {
                  let index = teachers.findIndex(r => r.mark === mark);
                  if (index > -1) {
                    teachers[index].List.push(sub)
                  } else {
                    teachers.push({
                      mark: mark,
                      List: [sub]
                    })
                  }
                } else {
                  let index = students.findIndex(r => r.mark === mark);
                  if (index > -1) {
                    students[index].List.push(sub)
                  } else {
                    students.push({
                      mark: mark,
                      List: [sub]
                    })
                  }
                }

                // this.microLessonDatas.push({
                //   ...item,
                //   src: vedioUrl + "/" + item["MLID"] + "." + item["FileFormat"],
                //   isLoading: false,
                //   isError: false,
                //   poster: defaultBg
                // });
              });
              if (teachers.length > 0) {
                this.microLessonDatas.push({ type: 'teacher', List: teachers });
              }
              if (students.length > 0) {
                this.microLessonDatas.push({ type: 'student', List: students });
              }
              console.log(this.microLessonDatas);
            }
          });
      },

      //切换微课视频
      vedioChange(e, index) {
        if (this.vedioIndex !== -1) {
          if (this.vedioIndex !== index) {
            if (this.vedioIndex !== -1) this.$refs["video" + this.vedioIndex][0].load();
            this.vedioIndex = index;
          }
        } else {
          this.vedioIndex = index;
        }
      },

      //数据加载出错
      handleError(e, index) {
        this.microLessonDatas[index].isLoading = false;
        this.microLessonDatas[index].isError = true;
      },

      //点击重新加载
      retry(e, index) {
        this.microLessonDatas[index].isError = false;
        setTimeout(() => {
          this.vedioIndex = index;
          if (this.vedioIndex !== -1) this.$refs["video" + this.vedioIndex][0].load();
        }, 200);
      },
    }
  };
</script>

<style lang="less" rel="stylesheet/less">
  #class-record {
    width: 955px;
    text-align: left;

    .main {
      .zlbk {
        padding-left: 10px;
        border-left: 3px solid #2a9efc;
      }

      .top {
        width: 955px;
        background: rgba(255, 255, 255, 1);
        border-radius: 3px;
        padding: 20px;
        font-size: 14px;
        color: #333;
        margin-bottom: 20px;
        overflow: hidden;

        .tt {
          margin-bottom: 27px;
        }

        .lname {
          margin-bottom: 25px;
        }

        .info {
          font-size: 12px;
          display: flex;
          justify-content: space-between;

          i {
            margin-right: 10px;
            color: #2a9efc;
          }
        }
      }

      .ktzy {
        margin-top: 30px;
        width: 956px;
        background: rgba(255, 255, 255, 1);
        border-radius: 5px;
        font-size: 14px;
        padding: 20px;

        .tt {
          color: #333;
          margin-bottom: 20px;
        }

        .bt {
          margin-bottom: 25px;
        }

        .nr {
          .item {
            width: 209px;
            height: 170px;
            background: rgba(241, 248, 255, 1);
            opacity: 0.8;
            border-radius: 3px;
            margin-right: 20px;
            display: inline-block;
            padding: 15px;
            margin-right: 15px;
            margin-bottom: 20px;
            overflow: hidden;

            .ct {
              text-align: center;

              .cdiv {
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;

                .cimg {
                  height: 100px;
                  max-width: 100%;
                }
              }

              .cdiv {
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;

                .cimg {
                  height: 100px;
                  max-width: 100%;
                }
              }

              .info {
                margin-top: 10px;
                line-height: 1.5;

                .name {
                  color: #444;
                }

                .time {
                  font-size: 12px;
                }
              }
            }

            .sp-name {
              margin-bottom: 20px;
            }
          }
        }
      }

      .title_first {
        color: #409EFF;
        font-size: 16px;
        padding-left: 10px;
        padding-right: 10px;
        line-height: 2;
      }

      .title_second {
        color: #67C23A;
        font-size: 16px;
        padding-left: 20px;
        padding-right: 10px;
        line-height: 2.5;
      }
    }
  }

  .dialog_list {
    /deep/ .el-table .cell.el-tooltip {
      white-space: inherit;
    }
  }

  .record_module {
    width: 100%;
    margin-top: 20px;
    padding: 20px;
    background: #fff;
    border-radius: 5px;

    .module_header {
      display: flex;
      justify-content: space-between;
      font-size: 14px;
      margin-bottom: 20px;

      .name {
        color: #333;
        font-size: 14px;
      }

      .more {
        color: #999;
        font-size: 12px;
      }
    }

    .blue_bg {
      border: none;
      background: rgba(241, 248, 255, 1);
    }
  }

  .sub_module {
    width: 100%;
    margin-top: 20px;
    background: #fff;
    border-radius: 5px;

    .module_header {
      display: flex;
      justify-content: space-between;
      font-size: 14px;
      margin-top: 20px;

      .title {
        padding-left: 10px;
        border-left: 3px solid #2a9efc;
        color: #333;
        font-size: 14px;
      }

      .more {
        color: #999;
        font-size: 12px;
      }
    }

    .blue_bg {
      border: none;
      background: rgba(241, 248, 255, 1);
    }
  }

  .loader {
    width: 58px;
    height: 58px;
    // background: rgba(15, 16, 17, 0.3);
    border-radius: 50%;
    position: relative;

    .ball-clip-rotate {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);

      > div {
        width: 15px;
        height: 15px;
        border-radius: 100%;
        margin: 2px;
        animation-fill-mode: both;

        border: 2px solid #fff;
        border-bottom-color: transparent;
        height: 26px;
        width: 26px;
        background: transparent !important;
        display: inline-block;
        animation: rotate 0.75s 0s linear infinite;
