  }

  .video-player {
    // width: 100%;
    // height: 188px;
    overflow: hidden;
    position: relative;
    // 封面
    .poster {
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: #fff;
    }

    video {
      width: 100%;
      height: 200px;
    }

    // 错误层
    .error {
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.84);
      position: absolute;
      left: 0;
      top: 0;
      z-index: 2;

      .lose {
        text-align: center;
        font-size: 15px;
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        line-height: 23px;
        margin-top: 63px;
        margin-bottom: 40px;
      }

      .retry {
        margin: 0 auto;
        text-align: center;
        line-height: 38px;
        width: 101px;
        height: 38px;
        background: rgba(255, 255, 255, 1);
        border-radius: 19px;
        font-size: 15px;
        color: #008eff;
      }
    }
  }

  .jptw,
  .taolun {
    .head {
      padding: 0 !important;
    }
  }
</style>



