<template>
  <div class="record_module">
    <div class="module_header">
      <div class="name">学生得分与点评</div>
      <div class="more" @click="MoreRanking()">
        查看更多
        <i class="iconfont icon-jiantou"></i>
      </div>
    </div>

    <el-row :gutter="20">
      <el-col :span="8" v-for="(item, index) in scorePreview" :key="index">
        <el-card shadow="hover" class="list_card blue_bg">
          <el-row :gutter="20">
            <el-col :span="12">
              <img src="/static/boy_img.png" alt=""/>
              <div>{{ item.StudentName}}</div>
            </el-col>
            <el-col :span="12" class="card_info">
              <!--              <div class="card_line">-->
              <div> 排名：{{item.Ranking}}</div>
              <div> 总分：{{item.TotalScore}}</div>
              <div> 评分：{{item.score ? item.score : '未评'}}</div>
              <div> 评价：{{item.evaluateText ? item.evaluateText : '未评'}}</div>
              <!--              </div>-->
            </el-col>
          </el-row>
        </el-card>
      </el-col>
    </el-row>

    <el-dialog title="学生得分明细" :visible.sync="dialogTableVisible" class="dialog_list" width="60%">
      <div style="text-align: right; margin-bottom: 10px;">
        <el-button type="primary" size="mini" @click="multiStudentEvaluate">批量点评</el-button>
      </div>
      <common-table :columns="tableColumns" :datas="studentList" th-class="table-innerhead" border max-height="550"
                    v-loading="listLoading" :select="true" :pagger="false" :data-id="keyIdName"
                    @selectHandler="selectChange">
        <template slot="oprate" slot-scope="scope" align="center">
          <el-button type="primary" size="mini" @click="studentEvaluate(scope.row)">学生点评</el-button>
        </template>
      </common-table>
    </el-dialog>

    <el-dialog title="学生点评" :visible.sync="dialogVisible" style="text-align: left;">
      <el-form ref="formComment" :model="formComment" :rules="rulesComment" label-width="80px">
        <el-form-item label="课程评分" prop="score">
          <el-input-number v-model="formComment.score" :min="1" :max="100" label="课程评分" controls-position="right"
                           :precision="0" :step="1"></el-input-number>
          分
        </el-form-item>
        <el-form-item label="学生评语" prop="comment">
          <el-input type="textarea" v-model="formComment.comment" :autosize="{ minRows: 3, maxRows: 10}"
                    placeholder="请输入学生评语"
                    maxlength="500" :show-word-limit="true"></el-input>
        </el-form-item>
        <el-form-item label="学生评价" prop="evaluate">
          <el-rate v-model="formComment.evaluate" :colors="colors" :texts="texts" show-text
                   style="margin-top: 10px;"></el-rate>
        </el-form-item>

        <div style="text-align:right;">
          <el-button @click="dialogVisible = false">取消</el-button>
          <el-button :loading="btnLoading" type="primary"
                     @click="selectedIds.length > 0 ? saveFormMulti('formComment') : saveForm('formComment')">
            {{formComment.id ? '更新评价' : '保存' }}
          </el-button>
        </div>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
  import commonTable from "@/components/commonTable";

  export default {
    name: "StudentScore",
    components: { commonTable },
    data() {
      return {
        course: JSON.parse(sessionStorage.getItem("course")),
        courseID: sessionStorage.getItem("courseID"),
        studentRanking: [],
        tableColumns: [
          { prop: "StudentName", label: "学生姓名", width: 80 },
          { prop: "Ranking", label: "排名", width: 76, sort: true },
          { prop: "SAScore", label: "截屏提问(分)", width: 126, sort: true },
          { prop: "GDScore", label: "小组讨论(分)", width: 126, sort: true },
          { prop: "PAScore", label: "随堂练习(分)", width: 126, sort: true },
          { prop: "TotalScore", label: "总分", width: 80, sort: true },
          { prop: "score", label: "评分", width: 80, sort: true },
          { prop: "evaluateText", label: "评价", width: 80, sort: true },
          { prop: "comment", label: "评语" },
          { prop: "oprate", label: "操作", slot: true, width: 100 }
        ],
        listLoading: false,
        dialogTableVisible: false,

        studentComments: [],
        dialogVisible: false,
        colors: ['#99A9BF', '#F7BA2A', '#FF9900'],
        texts: ['极差', '失望', '一般', '满意', '模范'],
        formComment: {
          id: "",
          courseID: "",
          studentID: "",
          comment: "",
          evaluate: 0,
          score: 100
        },
        rulesComment: {
          comment: {
            required: true,
            min: 1,
            max: 500,
            message: "长度在 1 到 500 个字符",
            trigger: "blur"
          },
          evaluate: [
            { required: true, type: 'number', message: "请选择学生评价" }
          ],
          score: [
            { required: true, message: "课程评分不能为空" },
            { type: "number", message: "课程评分必须为数字值" }
          ]
        },
        btnLoading: false,
        selectedIds: [],
        keyIdName: "StudentID",
      };
    },
    created() {
      this.GetStudentRanking();
      this.getAllStudentEvaluate();
    },
    methods: {
      //获取学生排名
      GetStudentRanking() {
        this.$http
          .get(this.$api.GetStudentRanking, {
            courseID: this.courseID,
            classID: this.course.ClassID
          })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach((item, index) => {
                this.studentRanking.push(item);
              });
            }
          });
      },
      //查看更多
      MoreRanking() {
        this.dialogTableVisible = true;
      },

      // 获取所有学生点评
      getAllStudentEvaluate() {
        this.studentComments = [];
        this.$http
          .get(this.$api.CourseCommentListByTeacher, {
            courseID: this.courseID
          })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.studentComments.push(item);
              });
            }
          });
      },
      selectChange(selected) {
        this.selectedIds = selected;
      },
      multiStudentEvaluate() {
        if (this.selectedIds.length < 1) {
          this.$message({ message: '请先选择学生', type: 'warning' });
        } else {
          let selectedCommenteds = this.studentComments.filter(r => this.selectedIds.indexOf(r["StudentID"]) > -1).map(r => r["CCID"]);
          if (selectedCommenteds.length > 0 && selectedCommenteds.length !== this.selectedIds.length) {
            this.$message({
              message: '未评价的学生和已评价的学生请分开评价！',
              type: 'warning'
            });
          } else {
            this.formComment = {
              id: selectedCommenteds.length > 0 ? selectedCommenteds.join(",") : "",
              studentID: selectedCommenteds.length > 0 ? "" : this.selectedIds.join(","),
              courseID: this.courseID,
              comment: "",
              evaluate: 0,
              score: 100
            };
            this.dialogVisible = true;
          }
        }
      },
      studentEvaluate(row) {
        this.formComment = {
          id: "",
          studentID: row["StudentID"],
          courseID: this.courseID,
          comment: "",
          evaluate: 0,
          score: 100
        };
        this.getStudentEvaluate();
        this.dialogVisible = true;
      },
      getStudentEvaluate() {
        let reqData = {
          courseID: this.formComment.courseID,
          studentID: this.formComment.studentID,
        };
        this.$http
          .get(this.$api.CourseCommentDataByTeacher, reqData)
          .then(res => {
            if (res.Result && res.Data) {
              this.formComment.id = res.Data["CCID"];
              this.formComment.comment = res.Data["Comment"];
              this.formComment.evaluate = res.Data["Evaluate"];
              this.formComment.score = res.Data["Score"];
            } else if (res.message) {
              this.$message({
                showClose: true,
                ...res
              });
            }
          });
      },
      saveFormMulti(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.btnLoading = true;
            let reqData = {
              ccIDs: this.formComment.id,
              courseID: this.formComment.courseID,
              studentIDs: this.formComment.studentID,
              score: this.formComment.score,
              comment: this.formComment.comment,
              evaluate: this.formComment.evaluate,
              anonymous: false,
              type: 1
            };
            this.$http
              .post(this.$api.CourseCommentMultiSave, reqData)
              .then(res => {
                if (res.Result) {
                  this.dialogVisible = false;
                  this.$message({
                    message: "提交成功！",
                    type: "success"
                  });
                  this.getAllStudentEvaluate();
                  this.selectedIds = [];
                } else if (res.message) {
                  this.$message({
                    showClose: true,
                    ...res
                  });
                }
                this.btnLoading = false;
              })
              .catch(err => {
                this.btnLoading = false;
              });
          } else {
            this.$message({
              showClose: true,
              message: "请输入正确格式",
              type: "error"
            });
            return false;
          }
        });
      },
      saveForm(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.btnLoading = true;
            let reqData = {
              ccID: this.formComment.id,
              courseID: this.formComment.courseID,
              studentID: this.formComment.studentID,
              score: this.formComment.score,
              comment: this.formComment.comment,
              evaluate: this.formComment.evaluate,
              anonymous: false,
              type: 1
            };
            this.$http
              .post(this.$api.CourseCommentSave, reqData)
              .then(res => {
                if (res.Result) {
                  this.dialogVisible = false;
                  this.$message({
                    message: "提交成功！",
                    type: "success"
                  });
                  this.getAllStudentEvaluate();
                } else if (res.message) {
                  this.$message({
                    showClose: true,
                    ...res
                  });
                }
                this.btnLoading = false;
              })
              .catch(err => {
                this.btnLoading = false;
              });
          } else {
            this.$message({
              showClose: true,
              message: "请输入正确格式",
              type: "error"
            });
            return false;
          }
        });
      },
    },
    computed: {
      studentList() {
        return this.studentRanking.map(item => {
          let index = this.studentComments.findIndex(r => r["StudentID"] === item["StudentID"]);
          let evaluate = index > -1 ? this.studentComments[index]["Evaluate"] : null;
          return {
            ...item,
            score: index > -1 ? this.studentComments[index]["Score"] : null,
            comment: index > -1 ? this.studentComments[index]["Comment"] : null,
            evaluate: evaluate,
            evaluateText: evaluate ? this.texts[evaluate - 1] : ""
          }
        });
      },
      scorePreview() {
        return this.studentRanking.filter((r, k) => k < 3).map(item => {
          let index = this.studentComments.findIndex(r => r["StudentID"] === item["StudentID"]);
          let evaluate = index > -1 ? this.studentComments[index]["Evaluate"] : null;
          return {
            ...item,
            score: index > -1 ? this.studentComments[index]["Score"] : null,
            comment: index > -1 ? this.studentComments[index]["Comment"] : null,
            evaluate: evaluate,
            evaluateText: evaluate ? this.texts[evaluate - 1] : ""
          }
        });
      }
    }
  }
</script>

<style lang="less" scoped>
  .list_card {
    text-align: center;

    .card_info {
      color: #2c3e50;
      line-height: 2;
      font-size: 14px;
      text-align: left;

      .card_line {
        display: flex;
        justify-content: space-between;
        font-size: 14px;
      }
    }

  }

  .dialog_list {
    /deep/ .el-table .cell.el-tooltip {
      white-space: inherit;
    }
  }
</style>



