<template>
  <div class="record_module">
    <div class="module_header">
      <div class="name">小组得分</div>
      <div>
        第
        <el-select v-model="groupCount" size="small" @change="getGroupRanking" :style="{width: '70px'}">
          <el-option v-for="(item, index) in groupList" :key="index" :label="item['GroupingNumber']"
                     :value="item['GroupingNumber']"></el-option>
        </el-select>
        次分组
      </div>
    </div>

    <el-row :gutter="20">
      <el-col :span="8" v-for="(item, index) in groupRanking" :key="index" :style="{padding:'10px'}">
        <el-card shadow="hover" class="list_card blue_bg">
          <el-row :style="{lineHeight: '35px'}">
            <el-col :span="20">
              {{item['GroupName']}}
            </el-col>
            <el-col :span="4">
              <el-tag v-if="item['Ranking']===1" size="small" type="danger"> {{item['Ranking']}}</el-tag>
              <el-tag v-else-if="item['Ranking']===2" size="small" type="success"> {{item['Ranking']}}</el-tag>
              <el-tag v-else-if="item['Ranking']===3" size="small"> {{item['Ranking']}}</el-tag>
              <el-tag v-else type="info" size="small"> {{item['Ranking']}}</el-tag>
            </el-col>
          </el-row>
          <div class="card_info">
            <div class="card_line">
              <div> 得分：{{item['TotalScore']}}</div>
              <div> 获赞：{{item['TotalGood']}}</div>
              <el-button size="mini" type="success" @click="viewMember(item)">成员</el-button>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <el-dialog title="小组成员得分" :visible.sync="dialogVisible">

      <el-table :data="memberList" max-height="550" v-loading="listLoading" stripe
                :default-sort="{prop: 'signTime', order: 'descending'}">
        <!--        <el-table-column type="index" width="70" label="序号">-->
        <!--        </el-table-column>-->
        <el-table-column property="Ranking" label="排名" sortable>
        </el-table-column>
        <el-table-column property="StudentName" label="学生姓名">
        </el-table-column>
        <el-table-column property="TotalGood" label="获赞" sortable>
        </el-table-column>
        <el-table-column property="TotalScore" label="总分" sortable>
        </el-table-column>

        <el-table-column property="IsGroupLeader" label="职位">
          <template slot-scope="scope">
            <el-tag :type="scope.row['IsGroupLeader'] ? 'success' : 'primary' " disable-transitions>
              {{ scope.row['IsGroupLeader'] ? '组长' : '成员'}}
            </el-tag>
          </template>
        </el-table-column>
      </el-table>

    </el-dialog>

  </div>
</template>

<script>
  export default {
    name: "GroupScore",
    props: {
      groupList: {
        type: Array,
        default: () => {
          return [];
        }
      }
    },
    data() {
      return {
        course: JSON.parse(sessionStorage.getItem("course")),
        courseID: sessionStorage.getItem("courseID"),
        groupCount: null,
        groupRanking: [],

        listLoading: false,
        memberList: [],
        dialogVisible: false,
      };
    },
    methods: {
      initSelect() {
        this.$nextTick(() => {
          if (this.groupList && this.groupList.length > 0) {
            this.groupCount = this.groupList[0].GroupingNumber;
            this.getGroupRanking();
          }
        });
      },
      //获取小组排名
      getGroupRanking() {
        this.groupRanking = [];
        this.$http.get(this.$api.GetGroupRanking, { courseID: this.courseID, number: this.groupCount })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.groupRanking.push(item);
              });
            }
          });
      },
      viewMember(item) {
        this.dialogVisible = true;
        this.listLoading = true;
        this.memberList = [];
        this.$http.get(this.$api.GetGroupRankingByMember, { groupID: item['GroupID'] })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.memberList.push(item);
              });
            }
            this.listLoading = false;
          }).catch(e => {
          this.listLoading = false;
        });
      }
    },
    watch: {
      groupList(newVal, oldVal) {
        this.initSelect();
      }
    }
  }
</script>

<style lang="less" scoped>
  .list_card {
    .card_info {
      color: #2c3e50;
      line-height: 2.5;

      .card_line {
        display: flex;
        justify-content: space-between;
        font-size: 14px;
      }
    }
  }
</style>



