<template>
  <div class="record_module">
    <div class="module_header">
      <div class="name">课程评价</div>
      <div class="more" @click="MoreData()">
        查看更多
        <i class="iconfont icon-jiantou"></i>
      </div>
    </div>

    <el-row :gutter="20">
      <el-col :span="8" v-for="(item, index) in scorePreview" :key="index">
        <el-card shadow="hover" class="list_card blue_bg">
          <el-row :gutter="20">
            <el-col :span="9">
              <img src="/static/boy_img.png" alt=""/>
              <div>{{ item['Datas']['IsAnonymous'] === 1 ? '匿名' : item.StudentName}}</div>
            </el-col>
            <el-col :span="15" class="card_info">
              <div class="card_line">
                <div>{{formatDate(item['Datas']['SubmitTime'], 'yyyy-MM-dd hh:mm') }}
                  <span class="card_key"> {{ item['Datas']['Score'] }}</span> 分
                </div>
                <p> {{item['Datas']['Comment']}}</p>
              </div>
            </el-col>
          </el-row>
        </el-card>
      </el-col>
    </el-row>

    <el-dialog title="课程评价记录" :visible.sync="dialogTableVisible" class="dialog_list">
      <common-table :columns="tableColumns" :datas="CourseCommentList" th-class="table-innerhead" border
                    max-height="550"
                    v-loading="listLoading" :select="false" :pagger="false">
        <template slot="StudentName" slot-scope="scope">
          {{scope.row['Datas']['IsAnonymous'] === 1 ? '匿名' : scope.row['StudentName'] }}
        </template>
        <template slot="Score" slot-scope="scope">
          {{scope.row['Datas']['Score']}}
        </template>
        <template slot="Comment" slot-scope="scope">
          {{scope.row['Datas']['Comment']}}
        </template>
        <template slot="SubmitTime" slot-scope="scope">
          {{formatDate(scope.row['Datas']['SubmitTime'], 'yyyy-MM-dd hh:mm')}}
        </template>
      </common-table>
    </el-dialog>

  </div>
</template>

<script>
  import commonTable from "@/components/commonTable";
  import { formatDate } from "@/utils/utils";
  import imgPhoto from "./../../../../../../../static/boy_img.png";

  export default {
    name: "CourseComment",
    components: { commonTable },
    data() {
      return {
        course: JSON.parse(sessionStorage.getItem("course")),
        courseID: sessionStorage.getItem("courseID"),
        CourseCommentList: [],
        tableColumns: [
          { prop: "StudentName", slot: true, label: "学生姓名", width: 120 },
          { prop: "Score", slot: true, label: "课程评分", width: 120, sort: true },
          { prop: "Comment", slot: true, label: "课程点评" },
          { prop: "SubmitTime", slot: true, label: "创建时间", width: 150, sort: true },
        ],
        listLoading: false,
        dialogTableVisible: false,
        defaultPhotoImg: imgPhoto,
      };
    },
    created() {
      this.GetCourseComment();
    },
    methods: {
      formatDate(dateValue, format) {
        return formatDate(dateValue, format);
      },
      //获取学生的课程评价
      GetCourseComment() {
        this.CourseCommentList = [];
        this.$http
          .get(this.$api.CourseCommentList, { courseID: this.courseID })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.CourseCommentList.push(item);
              });
            }
          });
      },
      //查看更多
      MoreData() {
        this.dialogTableVisible = true;
      },

    },
    computed: {
      scorePreview() {
        return this.CourseCommentList.filter((r, k) => k < 3);
      }
    }
  }
</script>

<style lang="less" scoped>
  .list_card {
    text-align: center;
    margin-bottom: 10px;

    .card_info {
      color: #2c3e50;
      background-color: #fff;
      border-radius: 10px;
      height: 106px;
      overflow-y: auto;
      word-break: break-all;
      word-wrap: break-word;

      .card_line {
        text-align: left;
        font-size: 12px;
        line-height: 1.5;

        div {
          color: #2a9efc;
        }

        .card_key {
          padding-left: 10px;
          color: #F56C6C;
        }

        p {
          font-size: 14px;
          padding-left: 10px;
          /*text-indent: 10px;*/
        }
      }
    }
  }

  .dialog_list {
    /deep/ .el-table .cell.el-tooltip {
      white-space: inherit;
    }
  }
</style>



