<template>
  <div class="sub_module">
    <div class="module_header">
      <div class="title">随堂练习</div>
    </div>

    <common-table :columns="tableColumns" :datas="dataList" th-class="table-innerhead" border :pagger="false"
                  max-height="550"
                  v-loading="dataLoading">
      <template slot="oprate" slot-scope="scope" align="center">
        <el-button type="success" size="mini" @click="previewAnswer(scope.row)">查看答题</el-button>
        <el-button type="success" size="mini" @click="getScoreStats(scope.row)">得分统计</el-button>
      </template>
    </common-table>

    <el-dialog title="答题明细" :visible.sync="dialogDetailVisible">
      <practice-detail ref="PracticeDetail" :cpdata="selectData"></practice-detail>
    </el-dialog>

    <el-dialog title="得分统计" :visible.sync="dialogScoreVisible">
      <el-table :data="dialogScoreList" max-height="550" v-loading="dialogScoreLoading" stripe
                :default-sort="{prop: 'Ranking', order: 'ascending'}">
        <el-table-column property="Ranking" label="排名"></el-table-column>
        <el-table-column property="StudentName" label="学生姓名"></el-table-column>
        <el-table-column property="SingleScore" label="单选题"></el-table-column>
        <el-table-column property="MultipleScore" label="多选题"></el-table-column>
        <el-table-column property="JudgeScore" label="判断题"></el-table-column>
        <el-table-column property="QandAScore" label="问答题"></el-table-column>
        <el-table-column property="TotalScore" label="总分"></el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
  import commonTable from "@/components/commonTable";
  import PracticeDetail from "./PracticeDetail";

  export default {
    name: "ClassPractice",
    components: { commonTable, PracticeDetail },
    data() {
      return {
        course: JSON.parse(sessionStorage.getItem("course")),
        courseID: sessionStorage.getItem("courseID"),
        dataList: [],
        dataLoading: false,
        tableColumns: [
          { prop: "CPTitle", label: "名称", width: 300 },
          { prop: "SubjectTotal", label: "题数" },
          { prop: "SubjectScore", label: "总分" },
          { prop: "CPTime", label: "计时(分)" },
          { prop: "oprate", label: "操作", slot: true, width: 240 }
        ],

        dialogScoreVisible: false,
        dialogScoreList: [],
        dialogScoreLoading: false,

        dialogDetailVisible: false,
        selectData: {}
      }
    },
    created() {
      this.getClassPracticeStats();
    },
    methods: {
      //获取随堂练习
      getClassPracticeStats() {
        this.dataLoading = true;
        this.dataList = [];
        this.$http
          .get(this.$api.ClassPractiseStatistics, { courseID: this.courseID, attribute: 2 })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.dataList.push({
                  CPID: item.Key.CPID,
                  PLID: item.Key.PLID,
                  CPTitle: item.PLTitle,
                  SubjectTotal: item.Total,
                  SubjectScore: item.Score,
                  CPTime: item.Key.CPTime,
                  CreateTime: item.Key.CreateTime,
                  State: item.Key.State
                });
              });
            }
            this.dataLoading = false;
          }).catch(e => {
          this.dataLoading = false;
        });
      },

      getScoreStats(row) {
        this.dialogScoreVisible = true;
        this.dialogScoreList = [];
        this.dialogScoreLoading = true;
        this.$http.get(this.$api.GetPAScoreStats, {
          courseID: this.CourseID,
          classID: this.course.ClassID,
          cpID: row.CPID
        }).then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.dialogScoreList.push(item);
            });
          }
          this.dialogScoreLoading = false;
        }).catch(e => {
          this.dialogScoreLoading = false;
        })
      },

      previewAnswer(row) {
        this.selectData = row;
        this.dialogDetailVisible = true;
        setTimeout(() => {
          this.$refs["PracticeDetail"].getSubjectData();
        }, 100);
      }
    }
  }
</script>

<style scoped>

</style>



