<template>
  <div class="main">
    <p class="title">{{title}}</p>

    <div class="list">
      <ul v-if="data.length>0" :style="{maxHeight: (height+(showDetail?40:0)) +'px'}">
        <li v-for="(item, index) in data" :key="index" @click="selectItem(item, index)">
          <div class="list_card">
            <img :src="item.photo" alt/>
            <p>{{item.name}}</p>
            <p class="list_line" v-if="showDetail">
              <span v-if="!item.score && item.score!==0">未评分</span>
              <span v-else>{{item.score||0}}分</span>
            </p>
          </div>
        </li>
      </ul>
      <div v-else style="text-align: center;">无数据</div>
    </div>

    <el-dialog title="学生作答点评" :visible.sync="dialogShow" width="800px" center append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="学生答案">
          <el-input v-if="subjectType!==4" type="textarea" :autosize="{ minRows: 1, maxRows: 5}" disabled
                    v-model="answer"></el-input>
          <div v-else  class="form_html">
            <div v-html="answer"></div>
          </div>
        </el-form-item>
        <el-form-item label="教师点评" prop="comment">
          <el-input type="textarea" :autosize="{ minRows: 1, maxRows: 5}" v-model="form.comment"></el-input>
        </el-form-item>
        <el-form-item label="答案得分" prop="score">
          <el-input-number v-model="form.score" :min="0" :max="150" controls-position="right" :precision="0"
                           :step="1"></el-input-number>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogShow = false">关 闭</el-button>
        <el-button v-loading="btnLoading" type="primary" @click="submitForm">更新点评</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import api from "../../../../../../utils/api"; 
  export default {
    name: "",
    props: {
      showDetail: {
        type: Boolean,
        default: false
      },
      title: {
        type: String,
        default: "答题人员列表"
      },
      height: {
        type: Number,
        default: 300
      },
      subjectType: {
        type: Number,
        default: 1
      },
      pictureType: {
        type: Number
      },
      subScore: {
        type: Number,
        default: 0
      },
      data: {
        type: Array,
        default: () => {
          return [
            { name: "A选项", photo: "" },
            { name: "B选项", photo: "" },
            { name: "C选项", photo: "" },
            { name: "D选项", photo: "" },
            { name: "E选项", photo: "" }
          ];
        }
      }
    },
    data() {
      return {
        series: [60, 50, 40, 30, 20, 10, 0],
        selectIndex: 0,
        dialogShow: false,
        btnLoading: false,
        answerType: 1,
        answer: "",
        form: {
          answerId: "",
          comment: "",
          score: 0
        },
        rules: {
          comment: {
            min: 0,
            max: 1000,
            message: "长度在 1000 个字符内",
            trigger: "blur"
          },
          score: {
            required: true,
            message: "请选择分数",
            trigger: "blur"
          }
        }
      };
    },
    methods: {
      selectItem(item, index) {
        this.selectIndex = index;
        let $this = this;
        if (this.showDetail) {
          this.answerType = item.type;

          if(item.content != null) {
            var htmstr = item.content;
            this.answer = htmstr.replace(/<img [^>]*src=['"]([^'"]+)[^>]*>/gi,
              function(match, capture) {  
                  var filename = capture.split('/')[1]; 
                  var newStr = '<img src="' + api.InteractDownload + '?filename=' + filename + '&fileType=' + $this.pictureType + '" alt="" style="width:380px"/>';
                  return newStr;
              }); 
          }

          this.form.answerId = item.answerId;
          this.form.comment = item.comment;
          this.form.score = item.score;

          this.dialogShow = true;
        }
      },
      submitForm() {
        this.$refs["form"].validate(valid => {
          if (valid) {
            this.$emit("submitSave", this.form);
          } else {
            this.$message({
              showClose: true,
              message: "请输入正确格式",
              type: "error"
            });
            return false;
          }
        });
      }
    }
  };
</script>
<style>
  @import "./../style/quill.snow.css";
</style>
<style lang="less" rel="stylesheet/less" scoped>
  .main {
    padding: 15px;
    text-align: center;
  }

  @baseWidth: 110px;
  @baseHeight: 140px;
  .list {
    padding: 0;
    margin: 0;
    width: 100%;

    ul {
      list-style: none;
      padding: 0;
      margin: 0;
      font-size: 14px;
      float: left;
      overflow-y: auto;
    }

    li {
      float: left;
      margin: 0 10px 10px 0;
      width: @baseWidth;
      height: @baseHeight;
      text-align: center;
      position: relative;
      background: rgba(52, 146, 255, 0.2);
      border-radius: 5px;

      .list_card {
        width: 100%;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        /*text-align: center;*/
        /*background: rgba(52, 146, 255, 0.2);*/

        img {
          margin: 5px 0;
          max-width: 80px;
          max-height: 80px;
          border-radius: 50%;
          border: 1px solid #fff;
        }

        p {
          margin: 0;
          line-height: 1.5;
        }

        .list_line {
          color: #f56c6c;
          padding-bottom: 5px;
        }
      }
    }
  }

  .form_img {
    margin: 5px 0;
    max-width: 500px;
    max-height: 80px;
  }
  .form_html{
    width: 100%;
    margin-top: 12px;
    line-height: 1.5;
    background-color: #F5F7FA;
    border:1px solid #E4E7ED;
    word-break: break-all;
    word-wrap: break-word;
  }
</style>



