<template>
  <div class="main">
    <p class="title">{{title}}</p>

    <div class="table">
      <div v-if="xSeries" class="series">
        <div v-for="(item, index) in series" :key="index">{{item}}人</div>
      </div>

      <div class="content">
        <ul class="chart"  :style="{height: height +'px'}">
          <li
            v-for="(item, index) in data"
            :key="index"
            :style="{width: (100.0/data.length)+'%'}"
            @click="selectChart(item, index)"
          >
            <!-- <span class="chart_value">{{item.value}}人</span> -->
            <span>{{item.percentage}}%</span>
            <div
              class="chart_line"
              :class="selectIndex===index?'active':''"
              :style="{height: height * item.percentage/100.0  +'px'}"
            ></div>
            <!-- <span>{{item.name}}</span> -->
          </li>
        </ul>

        <ul class="remark">
          <li
            v-for="(item, index) in data"
            :key="index"
            :style="{width: (100.0/data.length)+'%'}"
          >
            <span>{{item.name}}</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  props: {
    title: {
      type: String,
      default: "参与问卷结果"
    },
    xSeries: {
      type: Boolean,
      default: false
    },
    height: {
      type: Number,
      default: 300
    },
    data: {
      type: Array,
      default: () => {
        return [
          { name: "A选项", value: 42, percentage: 75 },
          { name: "B选项", value: 8, percentage: 10 },
          { name: "C选项", value: 6, percentage: 6 },
          { name: "D选项", value: 4, percentage: 5 },
          { name: "E选项", value: 2, percentage: 2 }
        ];
      }
    }
  },
  data() {
    return {
      //   title: "参与问卷结果",
      series: [60, 50, 40, 30, 20, 10, 0],
      //   dataList: [
      //     { name: "A选项", value: 42, percentage: 75 },
      //     { name: "B选项", value: 8, percentage: 10 },
      //     { name: "C选项", value: 6, percentage: 6 },
      //     { name: "D选项", value: 4, percentage: 5 },
      //     { name: "E选项", value: 2, percentage: 2 }
      //   ],
      selectIndex: 0
    };
  },
  methods: {
    selectChart(item, index) {
      this.selectIndex = index;
      this.$emit("chartChange", index);
    }
  }
};
</script>

<style lang="less" rel="stylesheet/less" scoped>
.main {
  //   position: absolute;
  //   top: 50%;
  //   left: 50%;
  //   transform: translate(-50%, -50%);

  //   background-color: rgba(255, 255, 255, 0.1);
  padding: 15px;
  text-align: center;
  font-weight: 100;
  //   color: #fff;
  .title {
    // font-size: 3.125rem;
    // margin-bottom: 2rem;
  }
}

.table {
  padding: 0;
  margin: 0;
  display: flex;
  align-items: flex-start;
  .series {
    min-width: 50px;
    text-align: right;
    // font-size: 1.25rem;
    line-height: 3;
  }
}

.content {
  width: 100%;
  padding-top: 20px;
}
.chart {
  list-style: none;
  padding: 0;
  margin: 0;
  //   min-height: 20rem;
  font-size: 14px;
  border-bottom: 2px solid #333;
  display: flex;
  align-items: flex-end;
  li {
    margin: 0 5px;
    // width: 30px;
    // font-size: 1.25rem;
    .chart_value {
      //   font-size: 2rem;
      // margin-top: -9rem;
    }
    .chart_line {
      background: #eee;
      /*background: rgba(52, 146, 255, 0.8);*/
    }
    .active {
      background-color: #67c23a;
    }
  }
}

.remark {
  list-style: none;
  padding: 0;
  //   font-size: 1.25rem;
  display: flex;
  align-items: flex-start;
  li {
    // width: 41px;
    margin: 0 5px;
    display: inline-block;
    text-align: center;
  }
}
</style>



