<template>
  <div id="class-screen">

    <div class="content">
      <div class="head">

        <div class="mb">
          <div class="mb_title">
            <img :src="getPicture(cpdata.SSContent)" alt/>
          </div>
          <div class="mb">
            答案：
            <span class="primary">{{cpdata.SSAnswer}}</span>
          </div>
        </div>

        <div class="part_title">学生答题统计</div>
        <div class="part_line">
          <el-row v-if="subjectType !== 4">
            <el-col :span="10">
              <r-chart ref="rchart" title="参与问卷结果" :data="answerStatistics" @chartChange="selectChart"></r-chart>
            </el-col>
            <el-col :span="14">
              <person-card title="对应答题人员列表" :data="answerPersons" :subject-type="subjectType" :picture-type="6"></person-card>
            </el-col>
          </el-row>
          <el-row v-else>
            <el-col :span="24">
              <person-card ref="Appraise" showDetail title="对应答题人员列表" :data="answerList" :subject-type="subjectType"
                           @submitSave="saveAppraise" :picture-type="6"></person-card>
            </el-col>
          </el-row>
        </div>

        <div class="part_title">问卷调研</div>
        <div class="part_line">
          <el-row>
            <el-col :span="4" class="line_left">已掌握</el-col>
            <el-col :span="16">
              <el-progress :percentage="surveyInfo.yesPer||0" :text-inside="true" :stroke-width="24"
                           status="success"></el-progress>
            </el-col>
            <el-col :span="4" class="line_right">{{surveyInfo.yes}}人</el-col>
          </el-row>
          <el-row>
            <el-col :span="4" class="line_left">未掌握</el-col>
            <el-col :span="16">
              <el-progress :percentage="surveyInfo.noPer||0" :text-inside="true" :stroke-width="24"
                           status="exception"></el-progress>
            </el-col>
            <el-col :span="4" class="line_right">{{surveyInfo.no}}人</el-col>
          </el-row>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
  import RChart from "./RChart";
  import PersonCard from "./PersonCard";
  import imgPhoto from "@/assets/boy_img.png";
  import { ModulePicture } from "@/utils/enum";

  export default {
    components: { RChart, PersonCard },
    props: {
      cpdata: {
        type: Object,
        default: () => {
          return {};
        }
      },
      subjectId: {
        type: String,
        default: ""
      }
    },
    data() {
      return {
        // cpdata: JSON.parse(sessionStorage.getItem("T_ScreenQuestion")),
        psdata: Object,
        // subjectId: this.$route.params.id,
        subjectAnswer: "",
        subjectType: "",
        answerStatistics: [],
        answerPersons: [],
        answerList: [],
        surveyInfo: {}
      };
    },
    created() {
      this.subjectType = this.cpdata["SSType"];
      if (this.subjectType !== 4) {
        this.getAnswerStatistics();
      } else {
        this.getAnswerList();
      }
      this.getSurveyList();
    },
    methods: {
      getAnswerStatistics() {
        this.answerStatistics = [];
        this.$http
          .get(this.$api.ScreenQuestionAnswerStatistics, { ssid: this.subjectId })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              let total = 0;
              res.Datas.forEach(r => {
                total += r["Total"] || 0;
              });

              let active = -1;
              res.Datas.forEach((item, k) => {
                let per = 0;
                if (total !== 0)
                  per =
                    Math.round((10000.0 * (item["Total"] || 0)) / total) / 100;
                let sub = {
                  name: item["OptionName"],
                  value: item["Total"],
                  percentage: per
                };
                this.answerStatistics.push(sub);
                if (active === -1 && (item["Total"] || 0) > 0) active = k;
              });
              if (active > -1 && this.subjectType !== 4)
                this.$refs["rchart"].selectChart(
                  this.answerStatistics[active],
                  active
                );
            }
          })
          .catch(err => {
          });
      },
      getAnswerDetail() {
        this.answerPersons = [];
        this.$http
          .get(this.$api.ScreenQuestionAnswerDetail, {
            ssid: this.subjectId,
            answer: this.subjectAnswer
          })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                let sub = {
                  id: item.Key["StudentID"],
                  name: item.Key["StudentName"],
                  photo: imgPhoto,
                  gender: item.Key["Gender"],
                  no: item.Key["Sno"]
                };
                this.answerPersons.push(sub);
              });
            }
          })
          .catch(err => {
          });
      },
      getAnswerList() {
        this.answerList = [];
        this.$http
          .get(this.$api.ScreenQuestionAnswerList, { ssid: this.subjectId })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                let sub = {
                  id: item.Key["StudentID"],
                  name: item["StudentName"],
                  photo: imgPhoto,
                  time: item.Key["SubmitTime"],
                  score: item.Key["SAScore"],
                  type: item.Key["SAForm"],
                  answerId: item.Key["SAID"],
                  content: item.Key["SAContent"],
                  comment: item.Key["Comment"]
                };
                this.answerList.push(sub);
              });
            }
          })
          .catch(err => {
          });
      },
      getSurveyList() {
        this.surveyInfo = {
          yes: 0,
          yesPer: 0,
          no: 0,
          noPer: 0,
          total: 0
        };
        this.$http
          .get(this.$api.ScreenQuestionSurveyInfo, { ssid: this.subjectId })
          .then(res => {
            if (res.Result) {
              this.surveyInfo = {
                yes: res["YesMaster"] || 0,
                yesPer: 0,
                no: res["NoMaster"] || 0,
                noPer: 0,
                total: 0
              };
              this.surveyInfo.total = this.surveyInfo.yes + this.surveyInfo.no;
              this.surveyInfo.yesPer =
                Math.round(
                  (10000.0 * this.surveyInfo.yes) / this.surveyInfo.total
                ) / 100;
              this.surveyInfo.noPer =
                Math.round(
                  (10000.0 * this.surveyInfo.no) / this.surveyInfo.total
                ) / 100;
            }
          })
          .catch(err => {
          });
      },

      //获取截屏提问模式
      fetchSSPattern(val) {
        let patternName = "";
        switch (val) {
          case 1:
            patternName = "全员作答";
            break;
          case 2:
            patternName = "随机作答";
            break;
          case 3:
            patternName = "抢答作答";
            break;
        }

        return patternName;
      },
      //获取题目类型
      fetchSSType(val) {
        let typeName = "";
        switch (val) {
          case 1:
            typeName = "单选题";
            break;
          case 2:
            typeName = "多选题";
            break;
          case 3:
            typeName = "判断题";
            break;
          case 4:
            typeName = "问答题";
            break;
        }

        return typeName;
      },
      selectChart(index) {
        this.subjectAnswer = this.answerStatistics[index].name;
        this.getAnswerDetail();
      },
      saveAppraise(form) {
        this.$refs["Appraise"].btnLoading = true;
        this.$http
          .post(this.$api.ScreenQuestionScoreSave, {
            said: form.answerId,
            saScore: form.score,
            comment: form.comment
          })
          .then(res => {
            if (res.Result) {
              this.$message({
                message: "操作成功！",
                type: "success"
              });
              let answer = this.answerList.find(
                r => r.answerId === form.answerId
              );
              answer.score = form.score;
              answer.comment = form.comment;
              this.$refs["Appraise"].dialogShow = false;
            } else if (res.message) {
              this.$message({
                showClose: true,
                ...res
              });
            }
            this.$refs["Appraise"].btnLoading = false;
          })
          .catch(err => {
            this.$refs["Appraise"].btnLoading = false;
          });
      },
      getPicture(val) {
        return `${this.$api.InteractDownload}?filename=${val}&fileType=${ModulePicture.ScreenSubject}&platFormType=1`;
      }
    }
  };
</script>

<style lang='less' scoped>
  #class-screen {
    text-align: left;

    .content {
      width: 100%;

      .head {
        padding: 20px;
        background: rgba(255, 255, 255, 1);
        font-size: 14px;

        .part_title {
          border-left: 2px solid #2a9efc;
          padding-left: 5px;
          margin: 5px 0;
        }

        .part_line {
          border-bottom: 2px solid #f1f5f9;

          .el-row {
            padding-bottom: 10px;
          }

          .line_left {
            text-align: right;
            padding-right: 10px;
          }

          .line_right {
            padding-left: 10px;
          }
        }

        .mb {
          margin-bottom: 15px;

          .primary {
            color: #409eff;
          }

          .danger {
            color: #f56c6c;
          }
        }

        .mb_title {
          text-align: center;

          img {
            padding: 10px;
            max-width: 800px;
            max-height: 600px;
          }
        }
      }
    }
  }
</style>



